import WarningIcon from "@mui/icons-material/WarningOutlined";
import ChatBubbleIcon from "@mui/icons-material/ChatBubbleOutline";
import { Box, Card, Chip, Divider, Stack, Typography } from "@mui/material";
import format from "date-fns-jalali/format";
import { Fragment, useState } from "react";
import { useNavigate, useLocation, useSearchParams } from "react-router-dom";
import TableLoader from "../../../../components/TableLoader";
import useAfterFirstRender from "../../../../utils/useAfterFirstRender";
import orderBy from "lodash/orderBy";


const List = ({
    loading,
    tickets
}) => {

    const navigate = useNavigate();
    const location = useLocation();

    const [searchParams, setSearchParams] = useSearchParams();

    const [sort, setSort] = useState(
        searchParams.has('sort') ? searchParams.get('sort') : '-createdAt'
    );


    useAfterFirstRender(() => {
        setSearchParams({
            ...Object.fromEntries(searchParams),
            sort
        })
    }, [sort]);


    if (loading) {
        return (
            <Card variant="outlined">
                <TableLoader />
            </Card>
        )
    }

    if (tickets.length < 1)
        return (
            <Card variant="outlined" sx={{ px: 5, py: 15, textAlign: "center" }}>
                <WarningIcon fontSize="large" />
                <p>موردی جهت نمایش وجود ندارد.</p>
            </Card>
        )

    return (
        <Card variant="outlined">
            {orderBy(tickets, 'openedAt', 'desc').map((ticket, index) => (
                <Fragment key={index}>
                    <Box
                        sx={{ p: 2 }}
                        onClick={() => navigate(
                            '/support/ticket/'.concat(ticket.id),
                            { state: { prev: location, ticket } }
                        )}
                    >
                        <Box
                            sx={{
                                display: 'flex',
                                justifyContent: 'space-between',
                                alignItems: 'center',
                                mb: 2
                            }}
                        >
                            <Typography variant="h6">{ticket.subject}</Typography>
                            <small>{format(new Date(ticket.openedAt), 'yyyy/MM/dd hh:mm')}</small>
                        </Box>
                        <Typography sx={{ mb: 2 }}>{ticket.messages[ticket.messages.length - 1].content}</Typography>
                        <Stack
                            spacing={1}
                            justifyContent="flex-start"
                            alignItems="flex-start"
                            direction="row"
                        >
                            <Chip
                                color={ticket.closedAt ? 'error' : 'success'}
                                label={
                                    <Box>
                                        وضعیت : <strong>{ticket.closedAt ? 'بسته شده' : 'باز'}</strong>
                                    </Box>
                                }
                                size="small"
                            />
                            <Chip
                                variant="outlined"
                                label={ticket.messages.length}
                                size="small"
                                sx={{ fontFamily: 'Lato, sans-serif' }}
                                icon={<ChatBubbleIcon />}
                            />
                        </Stack>
                    </Box>
                    <Divider />
                </Fragment>
            ))}
        </Card>
    )
}

export default List;