import Add from "@mui/icons-material/Add";
import HeadsetMic from "@mui/icons-material/HeadsetMicOutlined";
import { Box, Breadcrumbs, Button, Grid, Stack, Typography } from "@mui/material";
import { useEffect, useState } from "react";
import { useLocation, useNavigate, useSearchParams } from "react-router-dom";
import { useMiddleware } from "../../../../Application";
import { TabView } from "../../../../tabs";
import List from "./List";


const Main = ({ scope, adminWorkspace }) => {

    const location = useLocation();
    const navigate = useNavigate();

    const middleware = useMiddleware('tickets.'.concat(scope === "auth" ? "authenticated" : "main"));

    const [searchParams] = useSearchParams();

    const [tickets, setTickets] = useState({
        loading: true,
        data: [],
        total: ''
    });

    const loadDataAsync = async () => {
        const list = await middleware.list(
            Object.fromEntries([...searchParams]),
            adminWorkspace && {
                headers: {
                    'Workspace-ID': 'admin'
                }
            }
        );
        setTickets({
            loading: false,
            data: list.data,
            total: list.total
        });
    }

    useEffect(() => {
        loadDataAsync();
    }, [searchParams]);


    return (
        <TabView title="تیکت‌ها" Icon={HeadsetMic}>
            <Box sx={{ p: { xs: 1, sm: 2, md: 3 } }}>
                <Grid container justifyContent="center">
                    <Grid item xs={12} md={8}>
                        <Box
                            sx={{
                                mb: 1,
                                display: "flex",
                                justifyContent: "space-between",
                                alignItems: "center"
                            }}
                        >
                            <Box>
                                <Typography variant="h5" sx={{ fontWeight: 600, mb: 1 }}>
                                    تیکت‌ها
                                </Typography>
                                <Breadcrumbs>
                                    <Typography color="text.primary">پشتیبانی</Typography>
                                    <Typography color="text.primary">تیکت‌ها</Typography>
                                </Breadcrumbs>
                            </Box>
                            <Stack direction="row" spacing={1}>
                                {scope === "auth" && (
                                    <Button
                                        className="add-new"
                                        variant="transparent"
                                        startIcon={<Add />}
                                        onClick={() => navigate(
                                            '/support/tickets/new',
                                            { state: { prev: location } }
                                        )}
                                    >
                                        تیکت جدید
                                    </Button>
                                )}
                            </Stack>
                        </Box>
                        <List
                            loading={tickets.loading}
                            tickets={tickets.data}
                        // tickets={[{
                        //     subject: 'بخش اول درست کار نمیکند',
                        //     openedAt: new Date(),
                        //     message: 'سلام خوبی؟ چه خبر؟‌چکارا میکنی؟'
                        // }, {
                        //     subject: 'بخش اول درست کار نمیکند',
                        //     openedAt: new Date(),
                        //     message: 'سلام خوبی؟ چه خبر؟‌چکارا میکنی؟'
                        // }]}
                        />
                    </Grid>
                </Grid>
            </Box>
        </TabView>
    )
}

export default Main;